import getSymbolsInPatternWithPosition from '../getSymbolsInPatternWithPosition';

const getNextConsecutiveSymbols = (selectionStart, pattern, direction = 'default') => {
  const patternArray = getSymbolsInPatternWithPosition(pattern);

  let applicablePattern = patternArray.filter(symbol => symbol.index >= selectionStart);
  if (direction === 'reverse') {
    applicablePattern = patternArray.filter(symbol => symbol.index < selectionStart).reverse();
  }
  return countConsecutiveSymbols(selectionStart, applicablePattern, direction);
};

const countConsecutiveSymbols = (selectionStart, applicablePattern, direction) => {
  let cursor = selectionStart;
  let groupSize;

  for (groupSize = 0; groupSize < applicablePattern.length; groupSize += 1) {
    const condition = direction === 'default' ? cursor : cursor - 1;
    if (applicablePattern[groupSize].index === condition) {
      cursor = direction === 'default' ? cursor + 1 : cursor - 1;
    } else {
      // Exit if not consecutive.
      break;
    }
  }
  return groupSize;
};

export default getNextConsecutiveSymbols;



