import React, { Component } from 'react';
import classNames from 'classnames';
import { InputWithTextFormat, Select, Checkbox } from '..';

const PATTERNS = [
  { label: 'Credit card number **** **** **** ****', value: '**** **** **** ****' },
  { label: 'Card expiry **/**', value: '**/**' },
  { label: 'UK sort code **-**-**', value: '**-**-**' },
  { label: 'UK phone number +(**) **** *** ***', value: '+(**) **** *** ***' },
  { label: 'Brazilian CPF ID ***.***.***-**', value: '***.***.***-**' },
  { label: 'Chilean RUT ID **.***.***-*', value: '**.***.***-*' },
];

const PLACEHOLDERS = {
  '**** **** **** ****': 'Number on front of your card',
  '**/**': 'MM/YY',
  '**-**-**': '**-**-**',
  '+(**) **** *** ***': '+(**) **** *** ***',
  '***.***.***-**': '***.***.***-**',
  '**.***.***-*': '**.***.***-*',
};

const KNOBS = {
  knobs: [
    {
      type: 'select',
      label: 'Pattern',
      state: 'pattern',
      options: PATTERNS,
      defaultState: PATTERNS[3],
    },
  ],
};

export default class InputWithTextFormatDocs extends Component {
  constructor() {
    super();
    this.state = { ...this.getState() };
  }

  getState = () => {
    const state = [];
    // This function create a state array starting from the knobs config object. Each state property
    // is initialized with the default value specified in defaultState
    Object.values(KNOBS).map(knob =>
      knob.map(curr => {
        state[curr.state] = curr.defaultState;
        return state;
      }),
    );
    return state;
  };

  getDocs = () => {
    const docsString = Object.values(KNOBS)
      .map(knob => this.generateDocsWithUpdatedStateValue(knob).join('\n\r  '))
      .join();

    return (
      <pre className="tw-docs-code ">
        {`<InputWithTextFormat
  ${docsString}
/>`}
      </pre>
    );
  };

  generateDocsWithUpdatedStateValue = knob =>
    knob.reduce((acc, curr) => {
      const value =
        this.state[curr.state] && this.state[curr.state].value
          ? this.state[curr.state].value
          : this.state[curr.state];
      acc.push(`${curr.state} = {"${value || ''}"}`);
      return acc;
    }, []);

  generateInput = knobs => {
    const { type, label, state, options } = knobs;
    switch (type) {
      case 'select':
        return (
          <div className="col-md-6 m-b-2" key={state}>
            <label htmlFor={state} className="control-label">
              {label}
            </label>
            <Select
              id={state}
              selected={this.state[state] ? { ...this.state[state] } : undefined}
              options={options.map(option => ({ ...option }))}
              onChange={selection => {
                this.setState({
                  [state]: selection ? { ...selection } : undefined,
                });
              }}
            />
          </div>
        );

      case 'checkbox':
        return (
          <div className="col-md-6 m-b-2" key={state}>
            <label htmlFor={state} className="control-label">
              {label}
            </label>
            <Checkbox
              id={state}
              label={label}
              onChange={value => this.setState({ [state]: value })}
              checked={this.state[state]}
            />
          </div>
        );

      case 'input':
      default:
        return (
          <div className="col-md-6 m-b-2" key={state}>
            <label htmlFor={state} className="control-label">
              {label}
            </label>
            <input
              id={state}
              type="text"
              className="form-control"
              value={this.state[state]}
              onChange={ev => this.setState({ [state]: ev.target.value })}
            />
          </div>
        );
    }
  };

  handleOnChange = value => {
    // eslint-disable-next-line
    console.log('value', value);
  };

  render() {
    const { pattern } = this.state;
    return (
      <div className="container">
        <section className="section">
          {/* Full Component */}
          <div className="row">
            <div className="col-md-6">
              <h2>Input with text format</h2>
              <span role="img" aria-label="uhm">
                It&lsquo;s like having an argument with your partner. You say something and
                it&lsquo;s going to be turned into something else.
              </span>
              {getExtraDocs()}
              {this.getDocs()}
            </div>
            <div className="col-md-6">
              <div
                className={classNames({
                  ' m-b-4': true,
                  'droppable-dropping': this.state.isDropping,
                })}
              >
                <InputWithTextFormat
                  placeholder={pattern && PLACEHOLDERS[pattern.value]}
                  pattern={pattern && pattern.value}
                  ref={React.createRef()}
                  className="form-control"
                  onChange={value => this.handleOnChange(value)}
                />
              </div>
              <div className="row">{KNOBS.knobs.map(knob => this.generateInput(knob))}</div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

const getExtraDocs = () => (
  <div>
    <p className=" m-t-5">
      Input with text format is a component that formats text entered into controls. The model value
      is preserved without formatting, and validation continues to function on the unformated @@
    </p>
  </div>
);



