import React from 'react';
import Types from 'prop-types';

import {
  formatWithPattern,
  getNextConsecutiveSymbols,
  getSymbolsInSelection,
  getCursorPosition,
  unformatWithPattern,
} from '../common/textFormat';

import { HistoryNavigator } from './utils';

class InputWithTextFormat extends React.Component {
  static propTypes = {
    pattern: Types.string,
    className: Types.string,
    placeholder: Types.string,
    onChange: Types.func.isRequired,
    value: Types.string,
  };

  static defaultProps = {
    className: '',
    pattern: '',
    placeholder: '',
    value: '',
  };

  constructor(props) {
    super(props);
    const { value, pattern } = props;
    const unformattedValue = unformatWithPattern(value, pattern);
    this.input = React.createRef();
    this.state = {
      value: formatWithPattern(unformattedValue, pattern),
      isCutting: false,
      isPasting: false,
      pastedLength: 0,
      historyNavigator: new HistoryNavigator(),
      prevPattern: props.pattern,
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (prevState.prevPattern !== nextProps.pattern) {
      const { value, prevPattern, historyNavigator } = prevState;
      const { pattern } = nextProps;
      const unFormattedValue = unformatWithPattern(value, prevPattern);
      historyNavigator.reset();

      return {
        prevPattern: pattern,
        value: formatWithPattern(unFormattedValue, pattern),
        isCutting: false,
        isPasting: false,
        pastedLength: 0,
      };
    }
    return null;
  }

  setCursorPosition = select => {
    setTimeout(() => {
      this.input.current.setSelectionRange(select, select);
      this.setState({ selectionStart: select, selectionEnd: select });
    }, 0);
  };

  getKeyCombination = e => {
    const charCode = String.fromCharCode(e.which).toLowerCase();
    let currentAction = e.key;
    if ((e.ctrlKey || e.metaKey) && charCode === 'z') {
      currentAction = e.shiftKey ? 'Redo' : 'Undo';
    }
    if (e.ctrlKey && charCode === 'd') {
      currentAction = 'Delete';
    }
    return currentAction;
  };

  handleOnKeyDown = e => {
    const { selectionStart, selectionEnd } = this.input.current;
    const { historyNavigator } = this.state;
    const { pattern, onChange } = this.props;

    const currentAction = this.getKeyCombination(e);
    if (currentAction === 'Undo' || currentAction === 'Redo') {
      const value = historyNavigator.getHistory(currentAction);
      this.setState({ value });
      onChange(unformatWithPattern(value, pattern));
    }

    this.setState({
      currentAction,
      selectionStart,
      selectionEnd,
    });
  };

  handleOnPaste = e => {
    let pastedChar = e.clipboardData.getData('Text').split('');
    const { pattern } = this.props;
    // Remove symbols from cut text.
    pastedChar = pastedChar.filter(el => pattern.split('').indexOf(el) === -1);
    this.setState({ isPasting: true, pastedLength: pastedChar.length });
  };

  handleOnCut = () => {
    this.setState({ isCutting: true });
  };

  resetEvents = () => {
    this.setState({
      isCutting: false,
      isPasting: false,
      pastedLength: 0,
    });
  };

  isKeyAllowed = () => {
    const { currentAction } = this.state;
    const { pattern } = this.props;
    const symbolsInPattern = pattern.split('').filter(el => el !== '*');

    return symbolsInPattern.indexOf(currentAction) === -1;
  };

  handleOnChange = e => {
    if (!this.isKeyAllowed()) {
      return;
    }

    const { value } = e.target;

    const { pattern } = this.props;
    let unformattedValue = unformatWithPattern(value, pattern);

    const action = this.determineAction(unformattedValue);

    if (action === 'Backspace' || action === 'Delete') {
      unformattedValue = this.handleDelete(unformattedValue);
    }

    this.handleCursor(action);

    const formattedValue = formatWithPattern(unformattedValue, pattern);

    const { historyNavigator } = this.state;
    const { onChange } = this.props;

    this.setState(
      { value: formattedValue },
      this.resetEvents(),
      historyNavigator.setHistory(formattedValue),
      onChange(unformattedValue),
    );
  };

  handleDelete = unformattedValue => {
    const key = this.determineAction(unformattedValue);
    const { pattern } = this.props;
    const { selectionStart, selectionEnd } = this.state;
    const newStack = [...unformattedValue];
    if (selectionStart === selectionEnd) {
      let startPosition = selectionStart - getSymbolsInSelection(0, selectionStart, pattern);
      let toDelete = 0;
      let direction = 'default';
      if (key === 'Backspace') {
        startPosition -= 1;
        direction = 'reverse';
      }

      if (startPosition >= 0 && getNextConsecutiveSymbols(selectionStart, pattern, direction)) {
        toDelete = 1;
      }

      newStack.splice(startPosition, toDelete);
    }

    return newStack.join('');
  };

  handleCursor = action => {
    const { pattern } = this.props;
    const { selectionStart, selectionEnd, pastedLength } = this.state;

    const cursorPosition = getCursorPosition(
      action,
      selectionStart,
      selectionEnd,
      pattern,
      pastedLength,
    );

    this.setCursorPosition(cursorPosition);
  };

  determineAction = unformattedValue => {
    const { isPasting, isCutting, currentAction, value } = this.state;
    const { pattern } = this.props;
    let newKey;
    if (isPasting) {
      newKey = 'Paste';
    } else if (isCutting) {
      newKey = 'Cut';
    } else if (currentAction === 'Backspace' || currentAction === 'Delete') {
      newKey = currentAction;
    } else {
      // This is a fix for Android Browsers. At the moment there's no a real way to detect the backspace other than measure
      // the value length.
      newKey =
        unformattedValue.length <= unformatWithPattern(value, pattern).length
          ? 'Backspace'
          : currentAction;
    }
    return newKey;
  };

  render() {
    const { placeholder, className } = this.props;
    const { value } = this.state;

    return (
      <input
        ref={this.input}
        className={className}
        value={value}
        placeholder={placeholder}
        onPaste={e => this.handleOnPaste(e)}
        onKeyDown={e => this.handleOnKeyDown(e)}
        onChange={e => this.handleOnChange(e)}
        onCut={() => this.handleOnCut()}
      />
    );
  }
}

export default InputWithTextFormat;



